e reach 0.1 BUSD/DOD at the end? How does the mathematics work? ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"accordion-description"},[_c('div',{staticClass:"section"},[_c('div',{staticClass:"section-inner"},[_c('p',{staticClass:"rteBlock"},[_vm._v("The simple answer is because when you finally go to the fund pool to exchange BUSD, you can exchange 0.1 BUSD per DOD. The principle is this: we will continue to burn 99.999% of DOD. While burning, we will put 1 BUSD into the fund pool for every 1,000,000 burned. In the end, the BUSD in the fund pool will reach nearly a 0.1Billion while the DOD left is 1 billion, so 1 DOD ≈ $0.1.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"title-text"},[_c('div',[_vm._v("What if the holder does not sell, but waits for exchange when the price finally reaches $0.1? ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"accordion-description"},[_c('div',{staticClass:"section"},[_c('div',{staticClass:"section-inner"},[_c('p',{staticClass:"rteBlock"},[_vm._v("It only happens under extreme conditions, so there is no need to worry about it. When nobody sells DOD, the price of DOD will continue to increase until it reaches or approaches the critical value of $0.1. Of course, this is a conjecture, and it does not appear in reality, because $0.1 is the final price. In principle, it will not exceed $0.1, because, at this time, your choice would be selling it rather than holding it if you are a smart guy. Please believe it. Most people will calculate this result.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"title-text"},[_c('div',[_vm._v("What if the team holds 15% of the DOD to the final exchange fund pool? ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._sel