                                            class="m-font-size-16 font-size-16"
                                            m-font-size-set="true">DOD (Day of Defeat) is a token issued on the Binance Smart Chain. It is first and exciting social experiment on this planet, with a radical deflationary mathematical model which will keep the price of DOD token rising continuously. The team of DOD has fixed the first and final price in the smart contract mathematically. Therefore, all final holders will get 2,300,000 X gains.</span><span
