                                            style="color: rgb(255, 255, 255); display: initial; font-weight: 400;">A prize fund pool will be controlled by the smart contract. At the end of project, the prize fund pool will reach 100,000,000 BUSD while 1,000,000,000 DOD token left. At that point, all token holders can swap their DOD for BUSD at the rate 1 DOD = 0.1 BUSD. As we know , the price of strategic round is 0.0000000434 BUSD/DOD. Therefore, DOD will increase by about 2,300,000 times from the initial pre-sale price.</span><span
