                          <div class="u_FAQ dmRespRow fullBleedChanged fullBleedMode"
                               style="text-align: center;" id="FAQ" data-anchor="FAQ">
                            <div class="dmRespColsWrapper" id="1814330958">
                              <div class="dmRespCol small-12 u_1109147485 medium-12 large-12"
                                   id="1109147485">
                                <div class="dmNewParagraph u_1288255307" data-dmtmpl="true"
                                     data-element-type="paragraph" data-version="5"
                                     id="1288255307"
                                     style="transition: none 0s ease 0s; text-align: left; display: block;"
                                     data-uialign="center"><h2 class="text-align-center"><span
                                    style="color: rgb(56, 40, 28); display: initial;">FREQUENTLY ASKED QUESTIONS</span>
                                </h2>
                                </div>
                                <div class="u_1667570295 widget-832a29 dmCustomWidget"
                                     data-lazy-load="" id="1667570295"
                                     dmle_extension="custom_extension"
                                     data-element-type="custom_extension" wr="false"
                                     icon="false" surround="false"
                                     data-widget-id="832a29738c2b44fdb4e63369b1bf458a"
                                     data-widget-version="72"
                                     data-widget-config=""
                                     data-widget-initialized="">
                                  <ul class="accordion-wrapper roundedIcon  "
                                      data-first-expanded="false"
                                      ref="itemContainer"
                                  >
                                    <li @click="tapItem(0)" class="accordion-item"
                                        :class="{'expand': selectedIndex === 0}"
                                    >
                                      <div class="accordion-title">
                                        <div class="title-text">
                                          <div>How to buy Day Of Defeat "DOD" token?
                                          </div>
                                        </div>
                                        <div class="arrow-wrapper">
                                          <div class="arrow">
                                            <svg class="close"
                                                 xmlns="http://www.w3.org/2000/svg"
                                                 viewBox="0 0 1152 1792"
                                                 style="background-color:inherit !important"
                                                 fill="currentColor">
                                              <path fill="inherit"
                                                    d="M1075 736q0 13-10 23l-466 466q-10 10-23 10t-23-10l-466-466q-10-10-10-23t10-23l50-50q10-10 23-10t23 10l393 393 393-393q10-10 23-10t23 10l50 50q10 10 10 23z"></path>
                                            </svg>
                                          </div>
                                        </div>
                                      </div>
                                      <div class="accordion-description">
                                        <div class="section">
                                          <div class="section-inner"><p
                                              class="rteBlock">DOD is a token
                                            issued on the Binance Smart Chain, which
                                            could be purchased on PancakeSwap. The
                                            method is as follows: open the wallet,
                                            enter the wallet browsing interface,
                                            open PancakeSwap, choose to exchange
                                            with BNB or BUSD, enter the DOD contract
                                            address XXXXXXXXXXXXXXX in the tokens to
                                            be exchanged, and find the DOD, please
                                            use the contract to add instead of
                                            entering the token name, and then select
                                            the amount to exchange.</p></div>
                                        </div>
                                      </div>
                                    </li>
                                    <li @click="tapItem(1)" :class="{'expand': selectedIndex === 1}" class="accordion-item">
                                      <div class="accordion-title">
                                        <div class="title-text">
                                          <div>How would the price reach 0.1 BUSD/DOD
                                            at the end? How does the mathematics
                                            work?
                                          </div>
                                        </div>
                                        <div class="arrow-wrapper">
                                          <div class="arrow">
                                            <svg class="close"
                                                 xmlns="http://www.w3.org/2000/svg"
                                                 viewBox="0 0 1152 1792"
                                                 style="background-color:inherit !important"
                                                 fill="currentColor">
                                              <path fill="inherit"
                                                    d="M1075 736q0 13-10 23l-466 466q-10 10-23 10t-23-10l-466-466q-10-10-10-23t10-23l50-50q10-10 23-10t23 10l393 393 393-393q10-10 23-10t23 10l50 50q10 10 10 23z"></path>
                                            </svg>
                                          </div>
                                        </div>
                                      </div>
                                      <div class="accordion-description">
                                        <div class="section">
                                          <div class="section-inner"><p
                                              class="rteBlock">The simple answer
                                            is because when you finally go to the
                                            fund pool to exchange BUSD, you can
                                            exchange 0.1 BUSD per DOD. The principle
                                            is this: we will continue to burn
                                            99.999% of DOD. While burning, we will
                                            put 1 BUSD into the fund pool for every
                                            1,000,000 burned. In the end, the BUSD
                                            in the fund pool will reach nearly a
                                            0.1Billion while the DOD left is 1
                                            billion, so 1 DOD ≈ $0.1.</p></div>
                                        </div>
                                      </div>
                                    </li>
                                    <li @click="tapItem(2)" :class="{'expand': selectedIndex === 2}" class="accordion-item">
                                      <div class="accordion-title">
                                        <div class="title-text">
                                          <div>What if the holder does not sell, but
                                            waits for exchange when the price
                                            finally reaches $0.1?
                                          </div>
                                        </div>
                                        <div class="arrow-wrapper">
                                          <div class="arrow">
                                            <svg class="close"
                                                 xmlns="http://www.w3.org/2000/svg"
                                                 viewBox="0 0 1152 1792"
                                                 style="background-color:inherit !important"
                                                 fill="currentColor">
                                              <path fill="inherit"
                                                    d="M1075 736q0 13-10 23l-466 466q-10 10-23 10t-23-10l-466-466q-10-10-10-23t10-23l50-50q10-10 23-10t23 10l393 393 393-393q10-10 23-10t23 10l50 50q10 10 10 23z"></path>
                                            </svg>
                                          </div>
                                        </div>
                                      </div>
                                      <div class="accordion-description">
                                        <div class="section">
                                          <div class="section-inner"><p
                                              class="rteBlock">It only happens
                                            under extreme conditions, so there is no
                                            need to worry about it. When nobody
                                            sells DOD, the price of DOD will
                                            continue to increase until it reaches or
                                            approaches the critical value of $0.1.
                                            Of course, this is a conjecture, and it
                                            does not appear in reality, because $0.1
                                            is the final price. In principle, it
                                            will not exceed $0.1, because, at this
                                            time, your choice would be selling it
                                            rather than holding it if you are a
                                            smart guy. Please believe it. Most
                                            people will calculate this result.</p>
                                          </div>
                                        </div>
                                      </div>
                                    </li>
                                    <li @click="tapItem(3)" :class="{'expand': selectedIndex === 3}" class="accordion-item">
                                      <div class="accordion-title">
                                        <div class="title-text">
                                          <div>What if the team holds 15% of the DOD
                                            to the final exchange fund pool?
                                          </div>
                                        </div>
                                        <div class="arrow-wrapper">
                                          <div class="arrow">
                                            <svg class="close"
                                                 xmlns="http://www.w3.org/2000/svg"
                                                 viewBox="0 0 1152 1792"
                                                 style="background-color:inherit !important"
                                                 fill="currentColor">
                                              <path fill="inherit"
                                                    d="M1075 736q0 13-10 23l-466 466q-10 10-23 10t-23-10l-466-466q-10-10-10-23t10-23l50-50q10-10 23-10t23 10l393 393 393-393q10-10 23-10t23 10l50 50q10 10 10 23z"></path>
                                            </svg>
                                          </div>
                                        </div>
                                      </div>
                                      <div class="accordion-description">
                                        <div class="section">
                                          <div class="section-inner"><p
                                              class="rteBlock">First of all, we
                                            promise that the team will not redeem
                                            the fund pool. After calculation, the
                                            income we earn at the end of the project
                                            is already very substantial. There is no
                                            need to redeem the fund pool. In
                                            addition, we have a very substantial
                                            income, that is, the liquidity at the
                                            end of the project. The funds in the
                                            pool will also be very large. This fund
                                            is enough for everyone on the team to
                                            retire. Therefore, we don’t need to
                                            participate in the exchange fund pool at
                                            all. It is a gift for all the loyal fans
                                            who love this project, and you deserve
                                            it.</p></div>
                                        </div>
                                      </div>
                                    </li>
                                    <li @click="tapItem(4)" :class="{'expand': selectedIndex === 4}" class="accordion-item">
                                      <div class="accordion-title">
                                        <div class="title-text">
                                          <div>How to deal with DOD and BUSD in the
                                            liquidity pool at the end of the
                                            project?
                                          </div>
                                        </div>
                                        <div class="arrow-wrapper">
                                          <div class="arrow">
                                            <svg class="close"
                                                 xmlns="http://www.w3.org/2000/svg"
                                                 viewBox="0 0 1152 1792"
                                                 style="background-color:inherit !important"
                                                 fill="currentColor">
                                              <path fill="inherit"
                                                    d="M1075 736q0 13-10 23l-466 466q-10 10-23 10t-23-10l-466-466q-10-10-10-23t10-23l50-50q10-10 23-10t23 10l393 393 393-393q10-10 23-10t23 10l50 50q10 10 10 23z"></path>
                                            </svg>
                                          </div>
                                        </div>
                                      </div>
                                      <div class="accordion-description">
                                        <div class="section">
                                          <div class="section-inner"><p
                                              class="rteBlock">At the end of the
                                            project, we will calculate the number of
                                            Tokens remaining. For example, when the
                                            total remaining DOD is 1.5 billion and
                                            there are 500 million remaining in the
                                            Liquidity pool, then we all know there
                                            are 1 billion remaining outside. Now we
                                            may cancel the liquidity pool in advance
                                            and burn the 500 million DOD, and at the
                                            same time, BUSD would be put into the
                                            prize fund pool according to the ratio
                                            of destroying 1,000,000 into 1 BUSD. At
                                            this time, the project will end earlier
                                            (It is Just an example here, the
                                            situation may change in reality, but our
                                            plan is to proceed like this). The
                                            remaining BUSD would be used in 3 ways:
                                            firstly, team members, secondly,
                                            start-up capital, and thirdly,
                                            charitable donations.</p></div>
                                        </div>
                                      </div>
                                    </li>
                                    <li @click="tapItem(5)" :class="{'expand': selectedIndex === 5}" class="accordion-item">
                                      <div class="accordion-title">
                                        <div class="title-text">
                                          <div>Where does the 19% transaction tax go,
                                            does it burn or go to fund pool, and how
                                            to ensure security?
                                          </div>
                                        </div>
                                        <div class="arrow-wrapper">
                                          <div class="arrow">
                                            <svg class="close"
                                                 xmlns="http://www.w3.org/2000/svg"
                                                 viewBox="0 0 1152 1792"
                                                 style="background-color:inherit !important"
                                                 fill="currentColor">
                                              <path fill="inherit"
                                                    d="M1075 736q0 13-10 23l-466 466q-10 10-23 10t-23-10l-466-466q-10-10-10-23t10-23l50-50q10-10 23-10t23 10l393 393 393-393q10-10 23-10t23 10l50 50q10 10 10 23z"></path>
                                            </svg>
                                          </div>
                                        </div>
                                      </div>
                                      <div class="accordion-description">
                                        <div class="section">
                                          <div class="section-inner"><p
                                              class="rteBlock">The 19% deducted
                                            went into a wallet that we set up in
                                            advance. We will manually burn the DOD
                                            according to the amount of BUSD we sold.
                                            Initially, we planned to use smart
                                            contracts to complete all the work, but
                                            in the end, we found smart contracts is
                                            not a good choice because we require
                                            that every 1,000,000 DOD burned must be
                                            put into the fund pool 1 BUSD, but the
                                            price of DOD changes in real-time, and
                                            the smart contract cannot determine the
                                            amount of burn according to the price
                                            change. When the price is very low, we
                                            sell more DOD in exchange for more
                                            destruction. When the price rises, we
                                            can sell a small amount of DOD. This is
                                            a very complicated link and smart
                                            contracts cannot be completed, so It can
                                            only be done manually, so there is a
                                            certain delay when you see the amount of
                                            destruction and the number of fund pools
                                            displayed on the page. Maybe we update
                                            it once a day in the early stage, and
                                            maybe it will update it once an hour in
                                            the later period. We will make this
                                            address public to everyone. Everyone can
                                            check the DOD of the wallet. No one can
                                            earn personal gain here. We also hope
                                            that everyone in the community will
                                            supervise it.</p></div>
                                        </div>
                                      </div>
                                    </li>
                                    <li @click="tapItem(6)" :class="{'expand': selectedIndex === 6}" class="accordion-item">
                                      <div class="accordion-title">
                                        <div class="title-text">
                                          <div>Where does the project team come from,
                                            do you still need to recruit?
                                          </div>
                                        </div>
                                        <div class="arrow-wrapper">
                                          <div class="arrow">
                                            <svg class="close"
                                                 xmlns="http://www.w3.org/2000/svg"
                                                 viewBox="0 0 1152 1792"
                                                 style="background-color:inherit !important"
                                                 fill="currentColor">
                                              <path fill="inherit"
                                                    d="M1075 736q0 13-10 23l-466 466q-10 10-23 10t-23-10l-466-466q-10-10-10-23t10-23l50-50q10-10 23-10t23 10l393 393 393-393q10-10 23-10t23 10l50 50q10 10 10 23z"></path>
                                            </svg>
                                          </div>
                                        </div>
                                      </div>
                                      <div class="accordion-description">
                                        <div class="section">
                                          <div class="section-inner"><p
                                              class="rteBlock">The team members
                                            are mainly in the United States and
                                            Canada. We are not an organized team,
                                            but we all think that the guy who came
                                            up with this great idea is a genius. We
                                            think this project will be successful.
                                            We have no salary, only token
                                            distribution, but we believe we will
                                            earn enough pensions. We need more
                                            talents to join the team. We also need
                                            website designers, cryptocurrency
                                            engineers, professional marketing
                                            directors, legal consultants, art
                                            artists, video production experts, etc.
                                            We hope to build a global professional
                                            team, you can work from home and share
                                            team interests, because there are more
                                            great projects to be developed next. If
                                            you like this way of working and like
                                            our team, then you are welcome to email
                                            us and we will contact you.</p></div>
                                        </div>
                                      </div>
                                    </li>
                                    <li @click="tapItem(7)" :class="{'expand': selectedIndex === 7}" class="accordion-item">
                                      <div class="accordion-title">
                                        <div class="title-text">
                                          <div>The price rises 2,300,000 times.
                                            Really?
                                          </div>
                                        </div>
                                        <div class="arrow-wrapper">
                                          <div class="arrow">
                                            <svg class="close"
                                                 xmlns="http://www.w3.org/2000/svg"
                                                 viewBox="0 0 1152 1792"
                                                 style="background-color:inherit !important"
                                                 fill="currentColor">
                                              <path fill="inherit"
                                                    d="M1075 736q0 13-10 23l-466 466q-10 10-23 10t-23-10l-466-466q-10-10-10-23t10-23l50-50q10-10 23-10t23 10l393 393 393-393q10-10 23-10t23 10l50 50q10 10 10 23z"></path>
                                            </svg>
                                          </div>
                                        </div>
                                      </div>
                                      <div class="accordion-description">
                                        <div class="section">
                                          <div class="section-inner"><p
                                              class="rteBlock">We calculated based
                                            on the price of the strategic round. As
                                            we know the price is 0.0000000434
                                            BUSD/DOD, and the final price was 0.1
                                            BUSD/DOD, so we calculated that the DOD
                                            increased by 2,300,000X</p></div>
                                        </div>
                                      </div>
                                    </li>
                                    <li @click="tapItem(8)" :class="{'expand': selectedIndex === 8}" class="accordion-item">
                                      <div class="accordion-title">
