                                                 fill="currentColor">
                                              <path fill="inherit"
                                                    d="M1075 736q0 13-10 23l-466 466q-10 10-23 10t-23-10l-466-466q-10-10-10-23t10-23l50-50q10-10 23-10t23 10l393 393 393-393q10-10 23-10t23 10l50 50q10 10 10 23z"></path>
                                            </svg>
                                          </div>
                                        </div>
                                      </div>
                                      <div class="accordion-description">
                                        <div class="section">
                                          <div class="section-inner"><p
                                              class="rteBlock">We welcome a large
                                            number of whales to buy or sell, which
                                            will accelerate the progress of DOD
                                            destruction, and we are not worried
                                            about the price. The final price of $0.1
                                            is a fact that no one can change. As
                                            time goes on, DOD will continue to be
                                            burnt. And the price will continue to
                                            rise and approach $0.1/DOD, no whale can
                                            stop and change this process.</p></div>
                                        </div>
